import "./TheLostCity.css";
import jsgradient from "../../external-libraries/jsGradient.js";
import CONSTANTS from "../../Constants.json";
import { Bubbles } from "../Contents/Bubbles";

export const TheLostCity = () => {
    return (<div>
        {/* <ReversedBuildings towers={36}/> */}
        <Bubbles bubbles={5} />
        {/* <Temples temples={10} /> */}
    </div>)
}


const ReversedBuildings = ({ towers }) => {
    const max_z = 10;
    const gradients = jsgradient.generateGradient(CONSTANTS.BLACK, CONSTANTS.RAPTURE_DARK_BLUE, max_z + 1);
    function makeStacks(n, thr, h, fac=3, style=[]) {
        let acc_h = 0;
        return Array.from(Array(n)).map((_, i) => {
            const w_fac = i<=thr?1:fac;
            const stack_w = (80/(n+1)/w_fac)*(n+1-i);
            const stack_l = (100-stack_w)/2;
            const h_fac = i<=thr?1:fac;
            const stack_h = (h_fac*1)+Math.pow((i-n)/(Math.pow(Math.pow(n,4)/(h-1),1/4)),4);
            acc_h += stack_h;
            return (
                <div key={i} className={"inverted-tower-stacks"} style={{
                    width: `${stack_w}%`,
                    left: `${stack_l}%`,
                    height: `${stack_h}vh`,
                    bottom: `-${acc_h}vh`,
                    ...style,
                }}/>   
            )
        })
    }
    function makeInvertedBuildings(n, l_offset) {
        return Array.from(Array(n)).map((_, i) => {
            const w = 1+Math.random()*5;
            const spread = (i>=towers/2?l_offset+Math.random()*(50-l_offset):50+Math.random()*(50-l_offset));
            const h_curvature = 5.5;
            const z = (max_z/2)+Math.round((max_z/2)*Math.pow((spread-50)/(50-l_offset), 2));
            const rand_stacks = Math.round(Math.random()*4)+2;
            return (
                <div key={i}>
                    <div className={"inverted-tower"} style={{
                        left: `${spread-w/2}%`,
                        width: `${w}%`,
                        height: `${20+Math.pow((spread-50)/h_curvature, 2)}%`,
                        zIndex: z+Math.round(Math.random())-2,
                        background: gradients[z-(max_z/2)],
                    }}>
                        {makeStacks(rand_stacks, Math.round(Math.random()*rand_stacks), 5, 5, {background: gradients[z-5]})}
                    </div>
                </div>
            )
        });
    }
    return (
        <div className={"underwater-city-container"}>
            {makeInvertedBuildings(towers, 10)}
            {/* <div className={"fathomless-depths"} onClick={goToProject} /> */}
        </div>
    )
}

const Temples = ({ temples }) => {
    return <div>
        {Array.from(Array(temples)).map((_, i) => {
            const spread = (i>=temples/2?25+Math.random()*(50):50+Math.random()*(25));
            const w = 10 + Math.random() * 20;
            const h = 5 + Math.random() * 2;
            return <div key={i}>
                <div className={"temple"} style={{
                    width: `${w}%`,
                    height: `${h}%`,
                    left: `${spread - (w/2)}%`,
                    background: "black",
                }} />
                <div className={"obelisk"} style={{
                        background: "black",
                        width: "2%",
                        height: `${10 + Math.random() * 30}%`,
                        bottom: `${h}%`,
                        left: `${spread - (w/2) + 5}%`
                    }} />
            </div>
        })}</div>
}