import { useState } from "react";
import "./About.css";
import "./Skills.css";

const profColor ={
    1: "#ffffff",
    2: "#99b3c8",
    3: "#5181a7",
    4: "#256293",
    5: "#004175",
}

const skillIcons = {
    "javascript": { proficiency: 4 },
    "python": { proficiency: 4, children: ["snake", "twin", "eye", "eye2"] },
    "golang": { proficiency: 4, children: ["g", "o", "streaks"] },
    "reactjs": { proficiency: 4, children: ["base"] },
    "html": { proficiency: 4, children: ["bg", "line"] },
    "css": { proficiency: 4, children: ["bg", "line"] },
    "java": { proficiency: 3, children: ["cup", "cup1", "cup2", "cup3", "cup4", "holder", "flame", "flame1", "flame2"] },
    "git": { proficiency: 2, children: ["branch", "branch2"] },
    "mysql": { proficiency: 2 },
    "amazon-aws": { proficiency: 1, children: ["amazon-smile", "amazon-commissures"] },
    "amazon-alexa" : { proficiency: 1, children: ["amazon-smile", "amazon-commissures"] },
    "flask": { proficiency: 1 },
    "c": { proficiency: 1, children: ["c-circle"] },
    "c#": { proficiency: 1, children: ["c-circle", "sharp"] },
    "nodejs": { proficiency: 1, children: ["n", "o", "d", "e", "stick"] },
    "ocaml": { proficiency: 1, children: ["body", "head", "legs"] },
    "rust": { proficiency: 1, children: ["r-circle", "gear", "gear2", "gear3", "corner", "corner2", "r"] },
    "ruby": { proficiency: 1, children: ["center", "left", "right", "offset-l"] },
};

const otherIcons = {
    "word": { proficiency: 3 },
    "excel": { proficiency: 2 },
    "photoshop": { proficiency: 1 },
}

const references = {
    "programming": skillIcons,
    "other": otherIcons,
}

const PersonalSkill = ({ name, data, collection, setDesc }) => {
    let id = 0;
    return (
        <div>
            <div className={"personal-skill-icon"} title={!collection ? name : ""} 
                onMouseEnter={setDesc ? () => {setDesc(`${name} LEVEL ${data.proficiency}`)} : null}>
                <div key={id++} className={name+"-container skill"} style={{ background: `var(--${name}-logo-scheme)` }}>
                    {data.children?.map((cName, i) => {
                        return <div className={cName} key={i} />
                    })}
                </div>
            </div>
            {collection ? <div className={"proficiency-container"}>{Array.from(Array(5)).map((_, i) => {
                return <div key={i} className={"proficiency-bar"} style={{
                    background: i < data.proficiency ? profColor[i+1] : "",
                }} />
            })}</div> : ""}
        </div>
        
    )
}

const makeSkillDisplay = (arr, collection, reference, setDesc) => {
    return arr.map((s, j) => {
        return <PersonalSkill key={j} name={s} data={references[reference][s]} collection={collection} setDesc={setDesc}/>
    })
}

export const AboutContent = ({ showHobby }) => {
    return (
        <div className={"about-panel-container"}>
            <AboutHeader showHobby={showHobby} />
            <AboutSelection />
        </div>
    )
}

const AboutHeader = ({ showHobby }) => {
    return (
        <div className={"about-header-container"}>
            <div key={"title"} className={"about-header-title"}>
                {"Software Engineer - with a minor in mathematics"}
            </div>
            <div key={"art-music"} className={"arts-musics-container"}>
                <span className={"hobby"} onClick={showHobby}>{"Arts & Music"}</span>
                <span>{" enthusiast"}</span>
            </div>
        </div>
    )
}

const AboutSelection = () => {
    const [active, setActive] = useState("information");
    const selections = {
        "information": <Information />,
        "education": <Education />,
        "experience": <Experience />,
        "skills": <Skills />,
    }
    function makeSelectionList() {
        return Object.keys(selections).map((k, i) => {
            return <Selection key={i} cName={k} actv={active === k ? true : false} display={setActive} />;
        })
    }
    return (
        <div className={"about-selection-container"}>
            <div className={"selections"}>
                {makeSelectionList()}
            </div>
            <div className={"about-information-panel"}>
                {selections[active]}
            </div>
        </div>
    )
}

const Selection = ({ cName, actv, display }) => {
    const [hover, setHover] = useState(false);
    function mouseEnter() {
        if (actv) return;
        setHover(true);
    }
    function mouseLeave() {
        if (actv) return;
        setHover(false);
    }
    function mouseClick() {
        display(cName);
        setHover(false);
    }
    return (
        <div className={cName + "-icon-container about-selection"} style={{ opacity: actv ? 1 : (hover ? 0.7 : 0.3) }}
            onMouseLeave={mouseLeave} 
            onMouseEnter={mouseEnter}
            onClick={mouseClick}>
        </div>
    )
}

const Information = () => {
    return [              
            "Designed and developed the website with ReactJS.",
            "Rain, bubbles, fishes, and buildings were randomly generated with parabolic equations.",
            "All icons and shapes made purely from CSS; no image files were stored in repository.",
            "Return to the surface with the circular light atop, or delve into my projects with the light from the fathomless depth below."
    ].map((s, i) => {
        return (<span key={i}>{s}</span>)
    });
}

const Education = () => {
    function redirect(URL) {
        window.open(URL);
    }
    return [
        ["University of Maryland", "2016-2019", "https://www.umd.edu/"],
        ["Walla Walla Community College", "2015-2016", "https://www.wwcc.edu/"],
        ["Catholic Junior College", "2013-2014", "https://cjc.moe.edu.sg/"],
        ["Saint Joseph's Institution", "2011-2012", "https://www.sji.edu.sg/"],
    ].map((s, i) => {
        return <div className={"education-url"} key={i}>
            <span style={{ marginRight: "10px" }} onClick={() => {redirect(s[2])}}>{s[0]}</span><span>{s[1]}</span>
        </div>
    });
}

const Experience = () => {
    const [active, setActive] = useState(0);
    function showExperienceDetails(i) {
        setActive(i);
    }
    function experienceDetails(arr, l) {
        return (
            <div style={{ left: l, position: "relative", padding: "5px" }}>
                <div key={"0"}>
                    <span style={{ marginRight: "5px" }}>{arr[0]}</span>
                    <span>{arr[1]}</span>
                </div>
                <div className={"sub-details-container"} >
                    <div key={"1"} className={"experience-sub-details"}>
                        {arr[2].sub_details.map((a, i) => {
                            return <span key={i}>{a}</span>
                        })}
                    </div>
                    {arr[2].skills.length > 0 ? <div className={"skills-used-container"}> 
                        <span>{"Programming Languages:"}</span>
                        {makeSkillDisplay(arr[2].skills, false, "programming", null)}
                    </div> : ""}
                </div>
            </div>
        )
    }
    return [
        [
            "Tokopedia", 
            "Software Engineer", "2021-2022",
            {
                sub_details: [
                    "Backend engineer for Team Enterprise People & Team Enterprise Finance.",
                    "Developed and designed MySQL databases, RESTful API endpoints, as well as Unit and Integration testings in the three stages of environments: development, staging, and production.", 
                    "Implemented app's stability and health through alert and monitoring services (DataDog, NewRelic, Scalyr, and PagerDuty) which logged any incoming error messages.", 
                    "Maintained configuration and secret storing system which were updated regularly to match new and upcoming features.", 
                    "Researched ways to improve upon existing features, algorithms, and database structures, as well as to develop experimental features and custom libraries.",
                    "Reviewed and commented on peers' pull-requests to preserve code quality in the repositories.", 
                    "Recorded changes and additions of features within the app through API and Tech documentation.", 
                    "Attended daily scrum and sprint plannings to dicuss about new features to be developed, bugs to be fixed, and tools to be tested.",
                    "Worked closely with teams of frontend engineers and product managers to deliver new features and updates every months."
                ],
                skills: ["golang", "mysql", "git"],
            }
        ],
        [
            "Perception & Robotics Group @ UMD", 
            "Research Assistant", "2019-2022",
            {
                sub_details: [
                    "Developed and designed fully an open-source browser data-video visualizer called FEVA for a research purpose.",
                    "Developed a backend server API, capable of processing user configuration, editable data, and image assets into and from JSON files required for display.",
                    "Designed the frontend's UI/UX, code structure, as well as functions' logic and flows.", 
                    "Developed a frontend display, capable of displaying massive data in the form of videos, keypoints, labels, frames, and soundwaves.",
                    "Tool's features: ",
                    "Display dynamically sections of the video, from the specified number columns and rows to its resolution size.",
                    "Display frames of the video in a timeline strip with a customizable seconds interval to the lowest of 500ms.",
                    "Display keypoints of the human joints, in sync with the video playing, stored within the local JSON file.",
                    "Display editable, filterable, and draggable label data in a panel, adjusted to the specified time interval of the video.",
                    "Customizable color for labels along with working undo and redo functions to the changes history list."                     
                ],
                skills: ["python", "flask", "javascript", "html", "css", "reactjs", "git"],
            }
        ],
        [
            "University of Maryland, College Park", 
            "Tutor", "2018-2019",
            {
                sub_details: [
                    "Helped students with homeworks, exams, and concept problems for a range of subjects.", 
                    "Attended regular meet-ups to discuss about events and improvements for the tutoring center"
                ],
                skills: [],
            }
        ],
        [
            "Traits A.I.", 
            "AI Intern", "2018-2019",
            {
                sub_details: ["Developed an Amazon Alexa skill to educate consumer about Bitcoin."],
                skills: ["javascript", "amazon-aws", "amazon-alexa"]
            },
        ]
    ].map((s, i) => {
        return <div className={"experience-content"} key={i} style={{ 
                color: active == i ? "white": "", 
            }}>
            <span onClick={() => {showExperienceDetails(i)}}>{s[0]}</span>
            {active == i ? experienceDetails(s.slice(1, s.length), active == i  ? "2%": "") : ""}
        </div>
    });
}

const Skills = () => {
    const [active, setActive] = useState("programming-skills");
    const [desc, setDesc] = useState("-");
    const skillsMap = {
        "programming-skills": <ProgrammingSkills setDesc={setDesc} />,
        "others": <OtherSkills setDesc={setDesc} />
    }

    return (
        <div className={"skills-panel-container"}>
            <div className={"skill-description"}>{desc}</div>
            <div className={"skills-panel"}>
                <div className={"skill-selector-container"}>
                    {Object.keys(skillsMap).map((k, i) => {
                        return <div className={k+" selector"} key={i} style={{
                            opacity: active == k ? 1 : 0.3
                        }}
                        onClick={() => {setActive(k)}}>{k.replaceAll("-", " ")}</div>
                    })}
                </div>
                {skillsMap[active]}
            </div>
            
        </div>
    )
}

const ProgrammingSkills = ({ setDesc }) => {
    return <div className={"programming-skills-container"}>
        {makeSkillDisplay(Object.keys(skillIcons), true, "programming", setDesc)}
    </div>
}

const OtherSkills = ({ setDesc }) => {
    return <div className={"other-skills-container"}>
        {makeSkillDisplay(Object.keys(otherIcons), true, "other", setDesc)}
    </div>
}